@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  color: #fff;
  font-family: "Roboto", sans-serif;
  margin: 0;
}

.App-lists {
  margin-top: -150px;
}

.Header {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  background: transparent;
  transition: all ease 0.5s;
}
.black {
  background-color: #141414;
}
.Header-logo {
  height: 30px;
}
.Header-logo img {
  height: 100%;
}

.Header-avatar {
  height: 35px;
}

.Header-avatar img {
  height: 100%;
  border-radius: 3px;
}

.MovieRow {
  margin-bottom: 30px;
}

.MovieRow h2 {
  margin: 0px 0px 0px 30px;
}

.MovieRow-listarea {
  overflow-x: hidden;
  padding-left: 30px;
}

.MovieRow-list {
  transition: all ease 0.6s;
}
.MovieRow-movie {
  display: inline-block;
  width: 150px;
  cursor: pointer;
}

.MovieRow-movie img {
  width: 100%;
  transform: scale(0.9);
  transition: all ease 0.3s;
}

.MovieRow-movie img:hover {
  transform: scale(1);
}

.MovieRow-before,
.MovieRow-next {
  position: absolute;
  width: 40px;
  height: 225px;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
  opacity: 0;
  transition: all ease 0.5s;
}
.MovieRow-before {
  left: 0;
}
.MovieRow-next {
  right: 0;
}

.MovieRow:hover .MovieRow-before,
.MovieRow:hover .MovieRow-next {
  opacity: 1;
}

@media (max-width: 760px) {
  .MovieRow-before,
  .MovieRow-next {
    opacity: 1;
  }
}

.FeaturedMovie {
  height: 100vh;
}

.FeaturedMovie-overlay-y {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}
.FeaturedMovie-overlay-x {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 70px;
  padding-left: 30px;
  padding-bottom: 150px;
}

.FeaturedMovie-name {
  font-size: 60px;
  font-weight: bold;
}

.FeaturedMovie-info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.FeaturedMovie-rating,
.FeaturedMovie-year,
.FeaturedMovie-seasons {
  display: inline-block;
  margin-right: 15px;
}

.FeaturedMovie-rating {
  color: #46d369;
}

.FeaturedMovie-overview {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
}

.FeaturedMovie-buttons {
  margin-top: 15px;
}
.FeaturedMovie-watchbutton,
.FeaturedMovie-playlistbutton {
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  padding: 10px 25px;
  text-decoration: none;
  margin-right: 15px;
  border-radius: 5px;
  opacity: 1;
  transition: all ease 0.3;
}
.FeaturedMovie-watchbutton:hover,
.FeaturedMovie-playlistbutton:hover {
  opacity: 0.7;
}
.FeaturedMovie-watchbutton {
  background-color: #fff;
  color: #000;
}
.FeaturedMovie-playlistbutton {
  background-color: #333;
  color: #fff;
}

.FeaturedMovie-genre {
  margin-top: 15px;
  font-size: 18px;
  color: #999;
}

@media (max-width: 760px) {
  .FeaturedMovie {
    height: 90vh;
  }
  .FeaturedMovie-name {
    font-size: 40px;
  }
  .FeaturedMovie-info {
    font-size: 16px;
  }
  .FeaturedMovie-overview {
    font-size: 14px;
    max-width: 100%;
    margin-right: 30px;
  }
  .FeaturedMovie-playlistbutton,
  .FeaturedMovie-watchbutton {
    font-size: 10px;
  }
  .FeaturedMovie-genre {
    font-size: 14px;
  }
}

.Spinner {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Footer {
  margin: 20px 0;
  text-align: center;
}

.Footer a {
  color: white;
  text-decoration: none;
}
.Footer img {
  margin: 10px;
  transform: scale(0.9);
  transition: all ease 0.2s;
}

.Footer img:hover {
  transform: scale(1);
}

